import React from 'react';
import './About.css';

function About() {
  return (
    <div className="about-container">
      {/* Logo */}
      <div className="about-logo">
        <img src="/img7.jpg" alt="WinnerDream Logo" />
      </div>

      {/* Titre principal */}
      <h1 className="about-title">About WinnerDream Business Growth LLC</h1>

      {/* Introduction */}
      <p className="about-text">
        WinnerDream was created to empower African and global entrepreneurs with powerful online presence.
        From branding to LLC setup, we help businesses grow and shine.
      </p>

      {/* Mission */}
      <div className="about-section">
        <h2>Our Mission</h2>
        <p>
          To help small businesses and startups launch, grow, and scale online with professional tools and strategy.
        </p>
      </div>

      {/* Values */}
      <div className="about-section">
        <h2>Our Values</h2>
        <ul>
          <li>🌍 Inclusivity — We support everyone worldwide</li>
          <li>🤝 Trust — Your success is our success</li>
          <li>🚀 Performance — We deliver high-quality results</li>
          <li>💬 Support — Available via WhatsApp anytime</li>
        </ul>
      </div>

      {/* Founder */}
      <div className="about-section">
        <h2>Meet the Founder</h2>
        <p>
          “I built WinnerDream for every dreamer who wants to create something real. From Lomé to Virginia,
          our mission is to serve and empower.” 
        </p>
        <p className="about-signature">– Founder, WinnerDream Business Growth</p>
      </div>

      {/* Footer */}
      <footer className="about-footer">
        &copy; 2025 WinnerDream Business Growth LLC — All rights reserved.
      </footer>
    </div>
  );
}

export default About;



